@import 'variables';
@import 'fonts';
@import 'brand-icons';
@import 'solid-icons';

[class*=" #{$fa-css-prefix}-"],
[class^="#{$fa-css-prefix}-"]{
    -moz-osx-font-smoothing: grayscale;
    -webkit-font-smoothing: antialiased;
    display: inline-block;
    font-style: normal;
    font-variant: normal;
    text-rendering: auto;
    line-height: 1;

    &:before {
        font-size: 0.82em;
    }
}

/** Fix the umbraco tree icon display to allow for the alignment to work correctly **/
[class*=" icon-fa-"].umb-tree-icon:before,
[class^="icon-fa-"].umb-tree-icon:before {
    width: 20px;
    text-align: center
}